<script type="text/ecmascript-6">
  import '@/commons/base/computedRootFontSize.js'
  import '@/commons/expand/vue-expand'
  import '@/commons/bridge/bridge'
//  import $http from '@/commons/http/http'
  import '@/commons/filters/filters'

  import bannerImage0 from '../../assets/banner-1.jpg'
  import bannerImage1 from '../../assets/banner-2.jpg'

  import noticeImage0 from  '../../assets/highly-active.png'
  import noticeImage1 from  '../../assets/intelligent.png'
  import noticeImage2 from  '../../assets/shortcut.png'

  import platformImage0 from '../../assets/platform-0.jpg'
  import platformImage1 from '../../assets/platform-1.jpg'
  import platformImage2 from '../../assets/platform-2.jpg'
  import platformImage3 from '../../assets/platform-3.jpg'
  import platformImage4 from '../../assets/platform-4.jpg'

  import businessImage0 from '../../assets/business-0.jpg'
  import businessImage1 from '../../assets/business-1.jpg'
  import businessImage2 from '../../assets/business-2.jpg'

  import contactMapImage from '../../assets/map.png'
  import contactImage0 from '../../assets/service-hotline.png'
  import contactImage1 from '../../assets/contact-email.png'
  import contactImage2 from '../../assets/company-address.png'

  import qrCodeImageDriver from '../../assets/qr-code-driver.png'
  import qrCodeImageConsignor from '../../assets/qr-code-consignor.png'
  import copyrightImage from '../../assets/police-icon.png'

  export default {
    name: 'app',
    data () {
      return {
        currentNavIndex: 0,
        isScrolling: false,
        banner: {
          englishTitle: 'Everything is under control',
          chineseTitle: '',
          images: [bannerImage0, bannerImage1],
          height: '200px'
        },
        notices: [
          {
            image: noticeImage0,
            title: '',
            content: ''
          },
          {
            image: noticeImage1,
            title: '',
            content: '+'
          },
          {
            image: noticeImage2,
            title: '',
            content: '+'
          }
        ],
        platforms: [
          {
            image: platformImage0,
            title: '',
            content: '',
            slogan: '',
            isHover: false
          },
          {
            image: platformImage1,
            title: '',
            content: '',
            slogan: '',
            isHover:false
          },
          {
            image: platformImage2,
            title: '',
            content: '',
            slogan: '',
            isHover:false
          },
          {
            image: platformImage3,
            title: 'APP',
            content: '',
            slogan: '',
            isHover:false
          },
          {
            image: platformImage4,
            title: 'APP',
            content: '',
            slogan: '',
            isHover:false
          }
        ],
        businesses: [
          {
            image: businessImage0,
            title: '',
            content: ''
          },
          {
            image: businessImage1,
            title: '',
            content: ''
          },
          {
            image: businessImage2,
            title: '&',
            content: 'ETC'
          }
        ],
        contact: {
          contactMapImage: contactMapImage,
          contacts: [
            {
              image: contactImage0,
              title: '',
              content: '010-5082 2461'
            },
            {
              image: contactImage1,
              title: '',
              content: 'haoyunli@sinoiov.com'
            },
            {
              image: contactImage2,
              title: '',
              content: '827100085'
            }
          ],
          qrCodes: [
            {
              image: qrCodeImageConsignor,
              action: '',
              text: ''
            },
            {
              image: qrCodeImageDriver,
              action: '',
              text: ''
            }
          ]
        },
        copyrightImage: copyrightImage
      }
    },
    methods: {
      showPlatformTextWhenHover (index) {
        this.platforms[index].isHover = true
      },
      hidePlatformTextWhenHover (index) {
        this.platforms[index].isHover = false
      },
      linkTo(index) {
        this.currentNavIndex = index
        let domId
        switch (index) {
          case 0:
            domId = 'home'
            break
          case 1:
            domId = 'company-instruction'
            break
          case 2:
            domId = 'business-instruction'
            break
          case 3:
            domId = 'contact-us'
            break
          case 4:
            window.open('https://pay.utrailer.cn/', '_blank')
        }

        this.scrollTo(document.querySelector(`#${domId}`).offsetTop - 60)
      },
      scrollTo (scrollTop) {
        document.body.scrollTop = scrollTop
        document.documentElement.scrollTop = scrollTop

      },
    },
    mounted () {
      this.scrollTo(0)
      let scrollY
      window.addEventListener('scroll', () => {
        scrollY = window.scrollY
        this.isScrolling = scrollY > 0 && scrollY < 10

        let section1OffsetTop = document.querySelectorAll('.content-item')[0].offsetTop
        let section2OffsetTop = document.querySelectorAll('.content-item')[1].offsetTop
        let section3OffsetTop = document.querySelectorAll('.content-item')[2].offsetTop
        if (scrollY >= 0 && scrollY <= section1OffsetTop) {
          this.currentNavIndex = 0
        }

        if (scrollY > section1OffsetTop && scrollY < section2OffsetTop) {
          this.currentNavIndex = 1
        }

        if (scrollY >= section2OffsetTop && scrollY < section3OffsetTop) {
          this.currentNavIndex = 2
        }

        if (scrollY >= section3OffsetTop) {
          this.currentNavIndex = 3
        }
      })

      this.$nextTick(() => {
        window.setTimeout(() => {
          this.banner.height = `${this.$refs.imageRef[0].offsetHeight}px`
        }, 100)
        window.setTimeout(() => {
          console.dir()
        })
      })
    }
  }
</script>

